import React from "react";

const NotFoundPage = () => (
  <div className="not-found">
    <h1>404</h1>
    <h2>PAGE NOT FOUND</h2>
    <h4>You just hit a route that doesn&#39;t exist... the sadness.</h4>
  </div>
);

export default NotFoundPage;



