import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Label,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";
import { BHPIcon, Themes, ThemeManager } from "bhp-ui-react";

import { getPrefixedUrl } from "../Commons/helper";
import SideBar from "../Sidebar";
import Breadcrumbs from "../Breadcrumbs";

const gitHubLink = [
  { url: "https://github.com/BHP-DevHub/bhp-ui-react", name: "BHP-UI React" },
  { url: "https://github.com/BHP-DevHub/bhp-ui-angular", name: "BHP-UI Angular" },
  { url: "https://github.com/BHP-DevHub/bhp-ui-html", name: "BHP-UI HTML" },
  { url: "https://github.com/BHP-DevHub/bhp-ui", name: "BHP-UI CSS" }
];

export default class Layout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSideBarOpen: false,
      theme: Themes[0]
    };
  }

  toggleSidebar() {
    this.setState({ isSideBarOpen: !this.state.isSideBarOpen });
  }

  render() {
    const { pathname } = this.props;
    const { isSideBarOpen, theme } = this.state;
    return (
      <div className={"main-container"}>
        <SideBar
          className="side-bar"
          currentRoute={pathname}
          isSideBarOpen={isSideBarOpen}
          onToggle={this.toggleSidebar.bind(this)}
        />
        <div className={"main-body"}>
          <div className="header-container">
            <header className={"header"}>
              <div className={"left-side-bar-btn"} />
              <div className={"container title-layout"}>
                <div className={"title"}>
                  <BHPIcon
                    className="fas fa-bars align-middle expand-btn"
                    size={24}
                    onClick={this.toggleSidebar.bind(this)}
                  />

                  <h1>
                    <Link className={"header-link"} to={getPrefixedUrl("/")}>
                      Digital Design System
                    </Link>
                  </h1>
                </div>

                <div className={"theme-help"}>
                  <div className={"git-hub-link"}>
                    <Label for="git-hub-link">Repository:</Label>
                    <div className="pr-4">
                      <UncontrolledDropdown>
                        <DropdownToggle color="primary" caret>
                          Download
                        </DropdownToggle>
                        <DropdownMenu>
                          {gitHubLink.map((link, index) => (
                            <DropdownItem
                              key={link.name}
                              onClick={() => window.open(link.url, "_blank")}
                            >
                              {link.name}
                            </DropdownItem>
                          ))}
                        </DropdownMenu>
                      </UncontrolledDropdown>
                    </div>
                  </div>

                  <div className={"theme-picker"}>
                    <Label for="theme-name">Theme:</Label>
                    <div className="pr-4">
                      <ThemeManager theme={theme}>
                        <UncontrolledDropdown>
                          <DropdownToggle outline caret>
                            {theme.name}
                          </DropdownToggle>
                          <DropdownMenu>
                            {Themes.map((theme, index) => (
                              <DropdownItem
                                key={theme.name}
                                onClick={() => this.setState({ theme })}
                              >
                                {theme.name}
                              </DropdownItem>
                            ))}
                          </DropdownMenu>
                        </UncontrolledDropdown>
                      </ThemeManager>
                    </div>
                  </div>

                  <div className={"help"}>
                    <Link
                      className={"header-link"}
                      to={getPrefixedUrl("/help")}
                    >
                      Help
                    </Link>
                  </div>
                </div>
              </div>
            </header>
            <Breadcrumbs
              category="Foundation"
              slug="foundation/ui-colours"
              title="UI Colours"
              currentRoute={pathname}
            />
          </div>
          <div className={"body-container"}>
            {pathname === getPrefixedUrl("/") ? (
              this.props.children
            ) : (
              <div className="container">{this.props.children}</div>
            )}
          </div>
        </div>
      </div>
    );
  }
}



